import React, { useState } from 'react'
import styles from './styles.less'
import { Modal } from 'antd'

let data = [
    {
        title: '基础',
        lists: [
            {
                title: 'css选择器',
                content: <h1>这是内容</h1>
            },
            {
                title: 'css选择器',

            },
            {
                title: 'css选择器',

            },
            {
                title: 'css选择器',

            },
        ]
    }
]
data = data.concat(data, data)

export default function Css() {
    const [content, setContent] = useState<any>()
    const [modalVisible, setModalVisible] = useState(false)

    return (
        <div className={styles.root}>
            <Modal
                open={modalVisible}
                onCancel={() => {
                    setModalVisible(false)
                }}
                maskClosable={false}
                footer={null}
            >
                {content}
            </Modal>
            <header className={styles.header}>
                <h3>目录</h3>
            </header>
            <section className={styles.content}>
                {
                    data.map(({ title, lists }, dataIndex) => {
                        return <div className={styles.datasContainer} key={dataIndex}>
                            <h4>{title}</h4>
                            <div className={styles.dataContainer}>{
                                lists.map(({ title, content }, listsIndex) => {
                                    return <div
                                        onClick={() => {
                                            setContent(content);
                                            setModalVisible(true)
                                        }}
                                        className={styles.listsContainer}>
                                        <div className={styles.pre}>{dataIndex + 1}.{listsIndex + 1}</div>
                                        <div className={styles.listsTitle}>{title}</div>
                                    </div>
                                })
                            }</div>
                        </div>

                    })
                }

            </section>
        </div>
    )
}
