import React, { useState } from 'react'
import styles from './styles.less'
import { Tree } from 'antd'
import { DownOutlined } from '@ant-design/icons';
import type { DataNode, TreeProps } from 'antd/es/tree';
import Css from './components/base/cs';


const treeData: DataNode[] = [
    {
        title: '基础5件套',
        key: 'base',
        children: [{
            title: 'html',
            key: 'html'
        }, {
            title: 'css',
            key: 'css'
        },
        {
            title: 'js',
            key: 'js'
        }, {
            title: 'ts',
            key: 'ts'
        }, {
            title: 'react',
            key: 'react'
        }
        ]
    }, {
        title: '中高级技能',
        key: 'middle',
        children: [{
            title: 'webpack打包构建',
            key: 'webpack'
        }, {
            title: '代码层面优化',
            key: 'codeOptmize'
        }, {
            title: '打包优化',
            key: 'optmize'
        }, {
            title: '网络优化',
            key: 'netWorkOptmize'
        },
        ]
    }
]

export default function MyDemo() {
    const [nodeKey, setNodeKey] = useState('react-fiber')

    const onSelect = (e: any) => {
        setNodeKey(e)
    }
    return (
        <div>
            <h2>前端学习技能树</h2>
             <p className='desc'>替代浏览器收藏夹，以及一些详细的说明，达标要求等</p>
            <br />
            <div className={styles.container}>

                <div className={styles.left}>

                    <Tree
                        defaultExpandAll
                        showLine
                        switcherIcon={<DownOutlined />}
                        defaultExpandedKeys={[nodeKey]}
                        onSelect={onSelect}
                        treeData={treeData}
                    />
                </div>
                <div className={styles.right}>
                    <GetRightByKey comKey={nodeKey} />
                </div>
            </div>

        </div>
    )
}


function GetRightByKey({ comKey }: { comKey: string }) {
    const map: any = {
        'css': Css
    }
    const Coms = map[comKey] || (() => '所选节点在map中不存在该key')
    return <Coms />
}